import React from 'react';
import ReactDOM from 'react-dom';

import { Template } from "../Template/Template";
import "./styles.css";

import {AdText, Error, MagicProgress} from "../ad_text/ad_text";

import {
    ConfigProvider,
    AdaptivityProvider,
    AppRoot,
    Button, useAdaptivity, ViewWidth, Card, Tappable, Text, CardScroll, Link
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import profile_data from './posts_20211001.json';


function PostCard(props) {
    const { viewWidth } = useAdaptivity();
    const local_url = "public/anna_bark/" + props.post.local_url;

    return (
        <Card style={{width: viewWidth > ViewWidth.MOBILE ? '30%' : '70%', aspectRatio: '4/5'}}>
            <Text weight="semibold" style={{ marginTop: 10, marginBottom: 10, fontSize: 20, lineHeight: 1.5, textAlign:"center"}}>
                {props.name}: {props.post[props.data]}
            </Text>
            {/*<Link style={{flexShrink: 0}} href={props.post.shortcode}>{""}</Link>*/}
            <Tappable hasActive={false} hoverMode="opacity" Component="a" href={props.post.shortcode} target="_blank" style={{ borderRadius: 'inherit', width: '100%', height: '100%', display: 'block' }}>
                <img
                        src={local_url}
                        alt={props.name}
                        style={{width: '100%', height: '100%', objectFit: 'cover', borderRadius: 'inherit'}}
                />
            </Tappable>
            <Text weight="regular" style={{ marginTop: 10, fontSize: 12, lineHeight: 1.5}}>
                {props.post.caption}
            </Text>
        </Card>
    )
}


function TopPosts(props) {
    return (
        <React.Fragment>
            <h1 color="white" style={{ marginTop: 0 }}>{props.head}</h1>
            <CardScroll size="s" style={{marginRight: -20, marginLeft: -20, marginBottom: 40}}>
                {props.topPosts.map((item) => <PostCard post={item} name={props.name} data={props.data}/>)}
            </CardScroll>
        </React.Fragment>
    )
}



function TextGeneration() {
    const [progressBar, setProgressBar] = React.useState(false);
    const [progressStartTime, setProgressStartTime] = React.useState(null);
    const [adText, setAdText] = React.useState([]);
    const [error, setError] = React.useState("");


    const handleSubmit = (e) => {
        e.preventDefault();

        setProgressBar(true);
        setProgressStartTime(Date.now());
        setError("");
        setAdText([]);

        const requestOptions = {
            method: 'GET',
            credentials: 'include',
        };
        fetch("https://app.ykvlv.ai/api/generate_post_anna_bark", requestOptions)
            .then(res => res.json())
            .then(data => {
                setProgressBar(false);
                setError(data.error);
                setAdText(data.posts);
            });
    }

    return (
        <React.Fragment>
            <Button type="submit" size='l' onClick={handleSubmit}> </Button>
            {progressBar ? <MagicProgress start={progressStartTime}/> : <br/>}
            <Error error={error}/>
            <AdText adText={adText} isPaid={true} generationCount={0} freeGenerations={100} showFeedback={false}/>
        </React.Fragment>
    )
}


const App = () => {
    const top_comments = profile_data.posts.sort((a, b) => (a.comments < b.comments) ? 1 : -1).slice(0, 5);
    const top_likes = profile_data.posts.sort((a, b) => (a.likes < b.likes) ? 1 : -1).slice(0, 5);
    console.log(profile_data.posts.slice(0, 5));

    return (
        <AppRoot mode="partial">
            <Template projectName="ADTEXT" link="https://www.ykvlv.ai/ad_text">
                <h1 color="white" style={{ marginTop: 0 }}>Anna Bark.   </h1>
                <TextGeneration />
                <TopPosts head={"   "} name={""} data="likes" topPosts={top_likes} />
                <TopPosts head={"  "} name={""} data="comments" topPosts={top_comments} />
            </Template>
        </AppRoot>
    )
}

ReactDOM.render(
    <ConfigProvider>
        <AdaptivityProvider>
            <App />
        </AdaptivityProvider>
    </ConfigProvider>,
    document.getElementById('root')
);
