export function SignInWithGoogle(props) {
    const provider = new GoogleAuthProvider();
    // provider.addScope('https://www.googleapis.com/auth/contacts.readonly');
    const onClick = () => {
        // props.auth.setPersistence(props.auth.Auth.Persistence.SESSION)
        signInWithPopup(props.auth, provider)
            .then((result) => {
                const credential = GoogleAuthProvider.credentialFromResult(result);
                const token = credential.accessToken;

                const user = result.user;
                console.log(credential, user);
                props.auth.currentUser.getIdToken(true).then(function (idToken) {
                    console.log(idToken)
                })
            }).catch((error) => {
                console.log(error);
        });
    }

    return <Button mode="outline" before={<img src="/src/assets/google.svg" className="icon" alt="google auth" height={24}/>} size="l" onClick={onClick}></Button>
}

export function SignOutWithGoogle(props) {
    const onClick = () => {
        props.auth.signOut();
    }

    return <Button mode="outline" before={<img src="/src/assets/google.svg" className="icon" alt="google auth" height={24}/>} size="l" onClick={onClick}></Button>
}
