import React, {useEffect} from 'react';
import ReactDOM from 'react-dom';

import { Template } from "../Template/Template";
import {firebaseConfig, SignInWithGoogle, SignOutWithGoogle} from "./FirebaseAuth";
import "./styles.css";

import { initializeApp } from "firebase/app";
import { getAuth } from "firebase/auth";

import {
    ConfigProvider,
    AdaptivityProvider,
    AppRoot,
    Text,
    Input,
    Button,
    Card,
    Progress,
    Group,
    CardGrid,
    useAdaptivity,
    ModalRoot,
    ModalCard, ViewWidth, Textarea,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import {precisionRound} from "@vkontakte/vkui/dist/helpers/math";

initializeApp(firebaseConfig);

function AdTextCard(props) {
    return <React.Fragment>
        <Card size="l" mode="outline">
            <div style={{ marginTop:10, marginLeft:10, marginRight:10, marginBottom:10}}>
                <h2 color="white"> {props.index} </h2>
                {props.adText.split('\n').map(str => <p>{str}</p>)}
            </div>
        </Card>
    </React.Fragment>
}

export function AdText(props) {
    const { viewWidth } = useAdaptivity();

    if (!props.adText || props.adText.length === 0) {
        return <br/>
    }

    const generationCount = <Text weight="regular" style={{ marginBottom: 30, fontSize: 18, lineHeight: 1.5}}>
          {props.generationCount}  {props.freeGenerations}  .
    </Text>

    const premiumAccount = <Text weight="regular" style={{ marginBottom: 30, fontSize: 18, lineHeight: 1.5}}>
         !
        <br />  Premium        .
    </Text>

    return (
        <React.Fragment>
            <Group mode="card" className="AdText__results">
                <CardGrid size={viewWidth <= ViewWidth.MOBILE ? 'l' : 's'} style={{ alignItems: 'stretch' }}>
                    {props.adText.map((item, index) => <AdTextCard adText={item} index={index}/>)}
                </CardGrid>
            </Group>
            {!props.isPaid ? generationCount : premiumAccount}
            {props.showFeedback ? <Feedback /> : <br /> }
        </React.Fragment>
    )
}

function NeedPay(props) {
    const handleClick = () => {
        const requestOptions = {
            method: 'POST',
            credentials: 'include',
            body: JSON.stringify({
                'user_token_id': props.authToken
            })
        };
        fetch("https://app.ykvlv.ai/api/create_payment", requestOptions)
                .then(res => res.json())
                .then(data => {
                    if (data.error) {
                        console.log(data.error);
                    }
                    if (data.confirmation_url) {
                        window.location.href = data.confirmation_url
                    }
                });
    }

    return <Button size='l' onClick={handleClick}> Premium</Button>
}

export function Error(props) {
    if (props.error === "") {
        return <br/>
    }

    return (
        <Text weight="regular" style={{ marginTop: 20, marginBottom: 10, fontSize: 18, lineHeight: 1.5}}>
            {props.error}
        </Text>
    )
}

export function MagicProgress(props) {
    const [progress, setProgress] = React.useState(0);

    useEffect(() => {
        const interval = setInterval(() => setProgress(precisionRound((Date.now() - props.start) / 30000 * 100, 0)), 1000);
        return () => {
            clearInterval(interval);
        };
    }, []);

    return (
        <React.Fragment>
            <Text weight="regular" style={{ marginTop: 15, fontSize: 18, lineHeight: 1.5}}>
                ,  . . .   . . .
            </Text>
            <Progress value={progress} />
        </React.Fragment>
    )
}

function TextGeneration(props) {
    const [progressBar, setProgressBar] = React.useState(false);
    const [progressStartTime, setProgressStartTime] = React.useState(null);
    const [adText, setAdText] = React.useState([]);
    const [inputText, setInputText] = React.useState("");
    const [generationCount, setGenerationCount] = React.useState(0);

    const freeGenerations = 5;
    const [isPaid, setIsPaid] = React.useState(false);
    const [error, setError] = React.useState("");

    const handleChange = (event) => {
        setInputText(event.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!props.authData) {
            return props.setModal('auth');
        }

        setProgressBar(true);
        setProgressStartTime(Date.now());
        setError("");
        setAdText([]);

        // setTimeout(function(){
        //     setProgressBar(false);
        //     setIsPaid(true);
        //     setAdText([
        //         ",         :   .    ,    .      .      .",
        //         "       ,    .          .      .",
        //         "      .     : ,   .     10%   !"
        //     ]);
        //
        // }, 3000);

        const requestOptions = {
            method: 'POST',
            credentials: 'include',
            body: JSON.stringify({
                // 'user_email': props.authData.email,
                'user_token_id': props.authToken,
                'input_text': inputText
            })
        };
        fetch("https://app.ykvlv.ai/api/generate_post", requestOptions)
            .then(res => res.json())
            .then(data => {
                setProgressBar(false);
                setError(data.error);
                if (data.need_pay) {
                    props.setModal('pay');
                }
                setIsPaid(data.is_paid);
                setAdText(data.posts);
                setGenerationCount(data.generation_count);
            });
    }

    return (
        <React.Fragment>
            <Text weight="regular" style={{ marginBottom: 30, fontSize: 18, lineHeight: 1.5}}>
                          .
            </Text>
            <h2 color="white">  </h2>
            <form onSubmit={handleSubmit}>
                <Input onChange={handleChange} type="text" style={{ marginBottom: 10}} placeholder=",    " />
                <Button type="submit" size='l'> </Button>
                {progressBar ? <MagicProgress start={progressStartTime}/> : <br/>}
            </form>
            <Error error={error}/>
            <AdText adText={adText} isPaid={isPaid} generationCount={generationCount} freeGenerations={freeGenerations} showFeedback={true}/>
        </React.Fragment>
    )
}

function Feedback() {
    const [feedback, setFeedback] = React.useState("");
    const handleChange = (event) => {
        setFeedback(event.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        const requestOptions = {
            method: 'POST',
            headers: {
                'Authorization': 'Bearer keyVWFbLtlqU1tMht',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "fields": {
                    "feedback": feedback,
                }
            })
        };

        fetch("https://api.airtable.com/v0/appPsBMka6L5vpHXI/feedback", requestOptions).then(res => setFeedback("   !"));
    }

    return (
        <React.Fragment>
            <Text weight="regular" style={{ marginTop: 150, marginBottom: 10, fontSize: 16, lineHeight: 1.5}}>
                ,        .
                <br/>      !
            </Text>
            <form onSubmit={handleSubmit} style={{ marginBottom: 50}}>
                <Textarea onChange={handleChange} value={feedback} type="text" style={{ marginBottom: 5}} />
                <Button mode="outline" type="submit" size='m'> </Button>
            </form>
        </React.Fragment>
    )
}

const App = () => {
    const [authData, setAuthData] = React.useState(null);
    const [authToken, setAuthToken] = React.useState(null);
    const [modal, setModal] = React.useState(null);

    const auth = getAuth();
    auth.onAuthStateChanged((res) => {
        setAuthData(res);
        if (res) {
            res.getIdToken(true).then(idToken => {
                if (modal === 'auth') {
                    setModal(null);
                }
                setAuthToken(idToken)
            })
        }
    });

    const authorizationButton = (!authData ? <SignInWithGoogle auth={auth} /> : <SignOutWithGoogle auth={auth} />);
    const legalInfo = <React.Fragment>
        <a href="/terms_of_use.pdf"> </a>
        <br/>   
        <br/>  720416019476
    </React.Fragment>

    return (
        <AppRoot mode="partial">
            <Template legalInfo={legalInfo} authorizationButton={authorizationButton} projectName="ADTEXT" link="https://www.ykvlv.ai/ad_text">
                <h1 color="white" style={{ marginTop: 0 }}>  </h1>
                <TextGeneration authData={authData} authToken={authToken} setModal={setModal}/>
            </Template>
            <ModalRoot activeModal={modal}>
                <ModalCard
                        className="Modal"
                        actions={<SignInWithGoogle auth={auth}/>}
                        header=",   "
                        nav="auth"
                        onClose={() => setModal(null)}
                />
                <ModalCard
                        className="Modal"
                        actions={<NeedPay authToken={authToken}/>}
                        header="    .      Premium ."
                        nav="pay"
                        onClose={() => setModal(null)}
                />
            </ModalRoot>
        </AppRoot>
    )
}

ReactDOM.render(
    <ConfigProvider>
        <AdaptivityProvider>
            <App />
        </AdaptivityProvider>
    </ConfigProvider>,
    document.getElementById('root')
);
